
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import ChinaAddressV3Data from 'vux/src/datas/china_address_v3.json'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import value2name from 'vux/src/filters/value2name.js'

import api from '../../fetch/api'

export default {
data () {
return {
vSPGuid: '',
title: '个人中心页',
userName: '',
mobile: '',
address: ['广东省', '广州市', '天河区'],
address2: '',
dtAddress: '',
addressData: ChinaAddressV3Data,
moRem: true,
titleType: '',
isEdit: true,
addressTitle: '添加地址'
}
},
activated () {
this.vSPGuid = this.$route.query.productId
this.isEdit = localStorage.isEdit
let vReceiverGuid = localStorage.vReceiverGuid
if (this.isEdit === 'true') {
this.addressTitle = '编辑地址'
if (typeof (vReceiverGuid) !== 'undefined') {
this.getAddress(vReceiverGuid)
}
} else if (this.isEdit === 'false') {
this.addressTitle = '添加地址'
this.userName = ''
this.mobile = ''
this.dtAddress = ''
}
},
methods: {
saveOrEdit () {
this.isEdit = localStorage.isEdit
if (this.isEdit === 'true') {
this.editAddress()
} else if (this.isEdit === 'false') {
this.saveAddress()
}
},
saveAddress () {
this.address2 = this.getName(this.address).split(' ')
if (!this.address2[2]) {
this.address2.pop()
this.address2.push(this.address2[1])
}

api.addAddress({

consignee: this.userName,
province: this.address2[0],
city: this.address2[1],
area: this.address2[2],
detailAddress: this.dtAddress,
zipCode: '5',
phone: this.mobile,
isDefault: this.moRem
}).then(res => {
console.log('添加地址')
let that = this
if (res.code === 0) {
this.userName = ''
this.mobile = ''
this.dtAddress = ''
this.$msgbox('提示', res.message, function () {
that.$router.push('/addresslist')

})
}
console.log(res)
}, err => {
console.log('数据错误02')
console.log(err)
this.$msgbox('提示', err.data.message)
})
},

getAddress (vReceiverGuid) {

api.infoAddress({

vReceiverGuid: vReceiverGuid
}).then(res => {

if (res.code === 0) {
this.userName = res.data.consignee
this.mobile = res.data.phone
this.dtAddress = res.data.detailAddress
this.address = [res.data.province, res.data.city, res.data.area]
this.moRem = res.data.isDefault
}
}, err => {
this.$msgbox('提示', err.data.message)
})
},

editAddress () {
this.address2 = this.getName(this.address).split(' ')
if (!this.address2[2]) {
this.address2.pop()
this.address2.push(this.address2[1])
}

let vReceiverGuid = localStorage.vReceiverGuid
let than = this
api.upDateAddress({

vReceiverGuid: vReceiverGuid,
consignee: this.userName,
province: this.address2[0],
city: this.address2[1],
area: this.address2[2],
detailAddress: this.dtAddress,
zipCode: '5',
phone: this.mobile,
isDefault: this.moRem
}).then(res => {
if (res.code === 0) {
this.$msgbox('提示', res.message, function () {
than.$router.push('/addresslist')

})
}
}, err => {
this.$msgbox('提示', err.data.message)
})
},
getName (value) {
return value2name(value, ChinaAddressV3Data)
}
},
components: {
XInput,
Group,
XAddress,
XTextarea,
XHeader,
CheckIcon,
Cell
}
}



